.book {
  background-color: #162b2b;
  color: white;
  border-radius: 6px;
  padding: 3rem;
  margin: 3rem auto;
  max-width: 40rem;
}

.book p {
  font-size: 1.5rem;
}

.book h2 {
  font-size: 2.5rem;
}

.book figcaption {
  font-style: italic;
  font-size: 1.5rem;
  text-align: right;
  color: #a1e0e0;
}
